import { Button, Image, Modal } from "react-bootstrap";

export default function DocumentViewModal(props) {

  const download = async () => {
    try {
      const image = await fetch(props.data.imageUrl);
      const contentType = image.headers.get("Content-Type");

      if (contentType && contentType.startsWith("image/")) {
        const imageBlob = await image.blob();
        const fileExtension = props.data.image.split(".").pop();

        const imageURL = URL.createObjectURL(imageBlob);
        const link = document.createElement("a");
        link.href = imageURL;
        link.download = `image.${fileExtension}`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
      } else {
        console.error("Invalid or missing Content-Type header for the image");
      }
    } catch (error) {
      console.error("Error fetching or downloading the image", error);
    }
  };

  return (
    <Modal
      show={props.show}
      onHide={props.onHide}
      centered
      size="lg"
      backdrop="static"
    >
      <Modal.Header closeButton>
        <Modal.Title>Image Preview</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="py-2" >
          <Image src={props?.data?.imageUrl} alt="Image Preview" className="uploaded-img-layout"/>
        </div>
      </Modal.Body>
      <Modal.Footer>
        <Button
          className="float-end"
          style={{ background: "#6045EB" }}
          onClick={download}
        >
          Download Image
        </Button>
      </Modal.Footer>
    </Modal>
  );
}
